import mongoose from 'mongoose';

import { db_name, db_port } from '../config.json';
import categories from '../assets/categories'
import Question from '../models/Question';
import Category from '../models/Question';

export default () => {
  let db;
  mongoose.connect(`mongodb://localhost:${db_port}/${db_name}`, { useMongoClient: true }, (err, result) => {
    db=result;
    Category.populate(categories)
  });

  
}
